import React from 'react';
import { IconSettings, Icon, PageHeader, PageHeaderControl, Input, InputIcon, Button } from '@salesforce/design-system-react';
import './styles.css';

const Header = ({ search, count }) => {

    function handleOnChange(e) {
        const text = e.target.value.toLowerCase();
        search(text);
    }

    const controls = () => (
        <React.Fragment>
            <PageHeaderControl>
                <Button
                    assistiveText={{ icon: 'Change view' }}
                    iconCategory="utility"
                    iconName="table"
                    iconVariant="more"
                    variant="icon"
                    label="copy metadata"
                />
            </PageHeaderControl>
        </React.Fragment>
    );

    const actions = () => (
        <PageHeaderControl className="pageHeaderControl">
            <Button label="copy metadata" variant="brand" />
            <Input
                iconRight={
                    <InputIcon
                        assistiveText={{
                            icon: 'Search',
                        }}
                        name="search"
                        category="utility"
                    />
                }
                id="search"
                placeholder="search"
                onKeyUp={handleOnChange}
            />
        </PageHeaderControl>
    );

    const trail = [
        <spam><spam>Developer by: </spam> <a href="https://www.linkedin.com/in/willyanmarques/" target="_blank">Willyan Marques</a></spam>
    ];

    return (
        <IconSettings iconPath="/icons">
            <div className="pageHeader">
                <PageHeader
                    icon={
                        <Icon
                            assistiveText={{
                                label: 'code',
                            }}
                            category="standard"
                            name="apex"
                            style={{
                                backgroundColor: '#215ca0',
                                fill: '#ffffff',
                            }}
                            title="Home"
                        />
                    }
                    joined
                    onRenderActions={actions}
                    onRenderControls={controls}
                    trail={trail}
                    // label={`${count} items`}
                    info={`${count} items`}
                    title="Salesforce Metadata Types"
                    truncate
                    variant="object-home"
                />
            </div>
        </IconSettings>
    );

}

export default Header;